<template>
	<view class="container">
		<view class="title"><text>选择退款原因</text></view>
		<view class="prod-grid">
			<uni-grid :column="6" :highlight="true" @change="handleItemClick">
				<uni-grid-item :class="{ selected: chosedItem === index }"  v-for="(orderItem, index) in 3" :index="index" :key="index">
					<view class="grid-item-box" style="background-color: #fff;">
						<!-- <image :src="orderItem.pic"></image> -->
						<text>你麻痹</text>
					</view>
				</uni-grid-item>
			</uni-grid>
		</view>
		<form @submit="" class="reason-list">
			<radio-group @change="reasonChange">
				<block v-for="(reasonItem, index) in reasonList" :key="index">
					<view class="reason-item">
						<view class="reason">{{reasonItem.reason}}</view>
						<radio class="reason-radio" :value="index.toString()"
							:checked="index == currentReasonIndex ? true : false" />
					</view>
				</block>
			</radio-group>
			<button type="primary" @click="toAftersaleInfoPage" class="next-step">下一步</button>
		</form>
	</view>
</template>

<script>
	var http = require("../../utils/http.js");
	
	export default {
		data() {
			return {
				orderItems: [],
				aftersaleType: 0,
				reasonList: [],
				reasonCheckIndex: [],
				chosedItem: 0,
				currentReasonIndex: -1,
			}
		},

		onLoad: function(options) {
			uni.showLoading(); 
			this.setData({
				orderItems: JSON.parse(uni.getStorageSync("aftersaleOrderItems")) ,
				aftersaleType: options.aftersaleType,
			});
			for (var i = 0; i < orderItems.length; i++) {
				this.reasonCheckIndex.push(-1);
			}
			
			var params = {
				url: "/p/aftersale/reason/list",
				method: "GET",
				data: {
					aftersaleType: this.aftersaleType
				},
				callBack: res => {
					this.setData({
						reasonList: res,
					});
					uni.setStorageSync("aftersaleReasonList", JSON.stringify(res));
					uni.hideLoading();
				}
			};
			http.request(params);
		},
		
		methods: {
			handleItemClick: function(e) {
				this.chooseItem(e.detail.index);
			},
			
			chooseItem: function(index) {
				this.chosedItem = index;
				this.currentReasonIndex = this.reasonCheckIndex[this.chosedItem];
			},
			reasonChange: function(e) {
				this.reasonCheckIndex[this.chosedItem] = e.detail.value;
				this.currentReasonIndex = this.reasonCheckIndex[this.chosedItem];

			},
			
			toAftersaleInfoPage: function() {
				//校验哪个商品未选择理由
				for (var i = 0; i < this.reasonCheckIndex.length; i++) {
					if (this.reasonCheckIndex[i] === -1) {
						this.chooseItem(i);
						return;
					}
				}
				uni.navigateTo({
					url: "/pages/aftersale-apply/aftersale-info?reasonCheckIndex=" + this.reasonCheckIndex + "&aftersaleType=" + this.aftersaleType, 
				})
				
			}
		}
	}
</script>

<style lang="less">
	page {
		background-color: #f4f4f4;
	}

	.container {
		.prod-grid {
			.selected {
				border: 1px solid #ff6500;
			}
		}
		.title {
			font-weight: 800;
			text-align: center;
			margin-top: 40rpx;
			margin-bottom: 40rpx;
		}

		.reason-list {
			font-size: 28rpx;

			.reason-item {
				padding: 30rpx;

				display: flex;
				background-color: white;

				.reason-radio {
					margin-left: auto;
				}
			}

			.next-step {
				position: absolute;
				bottom: 0;
				width: 100%
			}
		}
	}
</style>